      display: flex;
      justify-content: center;
      align-items: center;
      li {
        margin: 0 0 0 18px;
        a {
          font-family: ar-reg;
          font-size: 12px;
          color: #939393;
          transition: all .3s ease-in-out;
          &:hover {
            color: #2170aa;
          }
        }
        &:last-child {
          margin: 0;
        }
      }
    }
    .lang-switcher {
      position: relative;
      outline: none;
      border: none;
      background-color: transparent;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 0 0 18px;
      cursor: pointer;
      .mr {
        margin: 0 0 0 3px;
      }
      img {
          
      }
      > span {
          font-family: ar-reg;
          font-size: 12px;
          color: #939393;
          padding: 2px 10px 0 0;
      }
      &::after {
        content: "î¡´";
        font-family: "Linearicons-Free";
        font-size: 8px;
        color: #939393;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: -18px;
      }
      .switch {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 40px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 9;
        width: 150px;
        border-radius: 5px;
        background-color: #fff;
        box-shadow: 0 0 16px rgba(0, 0, 0, 0.06);
        transition: all .5s ease-in-out;
        padding: 8px 0;
        a {
          display: flex;
          justify-content: center;
          align-items: center;
          font-family: ar-reg;
          font-size: 15px;
          color: #2e2e2e;
          margin: 3px 0;
          span {
            padding: 2px 10px 0 0;
          }
          img {
            width: 21px;
            border-radius: 2px;
          }
        }
        &::before {
          content: "";
          border-left: solid 7px transparent; 
          border-right: solid 7px transparent; 
          border-bottom: solid 7px #fff;
          filter: drop-shadow(0 0 16px rgba(0, 0, 0, 0.06));
          height: 0; 
          width: 0; 
          position: absolute;
          top: -7px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
      &:hover {
        .switch {
          opacity: 1;
          top: 30px;
          visibility: visible;
        }
      }
    }
  }
}
/*------ upper par end ------*/



