/*------ header start -------*/
.header {
  position: relative;
    .header-back {
        overflow: hidden;
        img {
          width: 100%;
          transition: all .5s ease-in-out;
          &:hover {
            transform: scale(1.02);
          }
        }
      }
    .header-inner {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      z-index: 7;
      text-align: center;
      h2 {
        margin: 0 0 9px 0;
        padding: 0;
        font-family: ar-bold;
        font-size: 48px;
        color: rgb(255, 255, 255);
        text-transform: uppercase;
        line-height: 1.182;
        cursor: default;
      }
      h3 {
        margin: 0 0 25px 0;
        padding: 0;
        font-family: ar-md;
        font-size: 30px;
        color: rgb(255, 255, 255);
        text-transform: uppercase;
        line-height: 1.05;
        cursor: default;
      }
      a {
        display: block;
        width: 164px;
        height: 44px;
        margin: 0 auto;
        border: none;
        border-radius: 50px;
        background-color: rgb(246, 127, 33);
        transition: all .5s ease-in-out;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: ar-md;
        font-size: 19.2px;
        color: #fff;
        span {
          margin: 0 0 0 7px;
          transition: all .5s ease-in-out;
        }
        &:hover {
          transform: scale(0.980);
          background-color: #2170aa;
          span {
            transform: rotate(-7deg);
          }
        }
      }
    }
    .owl-Carousel {
      .owl-nav {
        width: 100%;
        height: 0;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        .owl-prev, .owl-next {
          border-radius: 50%;
          background-color: rgba(0, 0, 0, 0.4);
          width: 48px;
          height: 48px;
          display: flex;
          justify-content: center;
          align-items: center;
          outline: none;
          transition: all .5s ease-in-out;
          span {
            font-size: 16px;
            color: #fff;
            transition: all .5s ease-in-out;
          }
          &:hover {
            background-color: #fff;
            span {
              color: rgb(46, 46, 46);
            }
          }
        }
        .owl-prev {
          position: absolute;
          right: 30px;
          opacity: 0;
          visibility: hidden;
        }
        .owl-next {
          position: absolute;
          left: 30px;
          opacity: 0;
          visibility: hidden;
        }
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        bottom: 40px;
        left: 50%;
        transform: translateX(-50%);
        .owl-dot {
          width: 9px;
          height: 9px;
          background-color: rgba(0, 0, 0, 0.502);
          border-radius: 50%;
          outline: none;
          margin: 0 2.5px;
          &.active {
            background-color: rgb(255, 255, 255);
          }
        }
      }
      &:hover {
        .owl-prev {
          opacity: 1;
          visibility: visible;
          right: 40px;
        }
        .owl-next {
          opacity: 1;
          visibility: visible;
          left: 40px;
        }
      }
    }
}
/*------ header end -------*/



