  .service .zx-container .serv .serv-text h5 {
    margin: 3px 0 0 0;
  }
  .footer .footer-slide .subscribe input {
    padding: 0 0 0 20px;
  }
  .footer .footer-slide .subscribe .send {
    left: unset;
    right: 6px;
  }
  .footer .footer-slide .icons .icon-container {
    margin: 0 5px 0 0;
    &:last-child {
