  }
  @media (max-width: 992px) {
    .nav-bar .zx-container .nav-icons {
      margin: 5px 0 0 auto;
    }
    .fixed-menu .menu {
      right: unset;
      left: -260px;
      &.show {
        right: unset;
        left: 0;
      }
    }
    .fixed-menu .menu .menu-upper .lang-switcher a img {
      margin: 0 0 0 5px;
    }
    .fixed-menu .menu .menu-list > ul {
      transform: translateX(-50%);
      &.active {
